import styled from 'styled-components';

const StyledRoot = styled.div`
    background-color: black;
    color: white;
    .main {
        max-width: 1100px;
        min-height: 500px;
        margin: auto;
        margin-top: 75px;
    }
`;

export default StyledRoot;


